import React, { useEffect, useState } from "react";
import { Layout, Row, Divider, Card, Col, Table } from "antd";
import axios from "axios";
import echarts from "echarts";
import styled from "styled-components";
import { getNumberWithDecimal } from "../../utils";
import { Statistic, LineChart } from "../../components";

const { Content } = Layout;

export const DetailsComponent = ({ className }) => {
  const [unrealizedDividend, setUnrealizedDividend] = useState("--");
  const [userTable, setUserTable] = useState([]);
  const [
    userUnrealizedDividendSeries,
    setUserUnrealizedDividendSeries,
  ] = useState([]);
  useEffect(() => {
    const fetchT1 = async () => {
      const [
        { data: unrealized_dividend },
        { data: user_unrealized_dividend },
        { data: user_table },
      ] = await Promise.all([
        axios.get(
          `https://raw.githubusercontent.com/odofmine/fund/main/t1/st/unrealized_dividend.json`
        ),
        axios.get(
          `https://raw.githubusercontent.com/odofmine/fund/main/t1/st/user_unrealized_dividend.json`
        ),
        axios.get(
          `https://raw.githubusercontent.com/odofmine/fund/main/t1/st/user_table.json`
        ),
      ]);
      if (unrealized_dividend.length > 0) {
        const unrealizedDividendValue = getNumberWithDecimal(
          unrealized_dividend[unrealized_dividend.length - 1][1],
          1
        );
        setUnrealizedDividend(unrealizedDividendValue);
      }
      const userUnrealizedDividendResult = Object.keys(
        user_unrealized_dividend
      );
      const userTable = Object.keys(user_table);
      const userTableData = userTable.map((item) => {
        const data = user_table[item];
        return {
          name: item,
          total: data[0],
          unrealizedPnl: data[1],
          unrealizedAllocation: data[2],
          real: data[3],
        };
      });
      const userTableDataTotal = userTableData.reduce(
        (acc, curr) => {
          return {
            name: "all",
            total: acc.total + curr.total,
            unrealizedPnl: acc.unrealizedPnl + curr.unrealizedPnl,
            unrealizedAllocation:
              acc.unrealizedAllocation + curr.unrealizedAllocation,
            real: acc.real + curr.real,
          };
        },
        {
          name: "all",
          total: 0,
          unrealizedPnl: 0,
          unrealizedAllocation: 0,
          real: 0,
        }
      );
      const series = userUnrealizedDividendResult.map((item) => {
        const data = user_unrealized_dividend[item];
        const step = (data[0][0] - 1588291200) / 60 / 60 / 24;
        const prefix = Array.from({ length: step }, (x, i) => [
          1588291200 + i * 24 * 60 * 60,
          0,
        ]);
        return {
          name: item,
          type: "line",
          areaStyle: {},
          data: [...prefix, ...data].map((x) => [
            x[0] * 1000,
            getNumberWithDecimal(x[1], 1),
          ]),
          stack: "userUnrealizedDividend",
          showSymbol: false,
          hoverAnimation: true,
          lineStyle: {
            width: 0,
          },
        };
      });
      setUserTable([...userTableData, userTableDataTotal]);
      setUserUnrealizedDividendSeries(series);
    };
    fetchT1();
  }, []);

  const userOption = {
    baseOption: {
      title: {
        text: "待分配利润",
        textStyle: {
          fontSize: 20,
        },
      },
      series: userUnrealizedDividendSeries,
      tooltip: {
        trigger: "axis",
        axisPointer: {
          type: "cross",
          label: {
            backgroundColor: "#6a7985",
          },
        },
      },
      legend: {
        right: 0,
        top: 3,
      },
      grid: {
        left: "3%",
        right: "4%",
        bottom: "3%",
        containLabel: true,
      },
      xAxis: {
        type: "time",
        boundaryGap: false,
        splitLine: {
          show: false,
        },
        axisLine: {
          lineStyle: {
            color: "#888",
          },
        },
      },
      yAxis: {
        type: "value",
        axisLabel: {
          show: true,
          formatter: "{value}",
        },
        splitNumber: 3,
        axisLine: {
          lineStyle: {
            color: "#888",
          },
        },
      },
    },
    media: [
      {
        query: {
          maxWidth: 760,
        },
        option: {
          title: {
            text: "待分配利润",
            textStyle: {
              fontSize: 12,
            },
          },
          series: userUnrealizedDividendSeries,
          tooltip: {
            trigger: "axis",
            axisPointer: {
              type: "cross",
              label: {
                backgroundColor: "#6a7985",
              },
            },
          },
          legend: {
            right: 0,
            top: 3,
          },
          grid: {
            left: "3%",
            right: "4%",
            bottom: "3%",
            containLabel: true,
          },
          xAxis: {
            type: "time",
            boundaryGap: false,
            splitLine: {
              show: false,
            },
            axisLine: {
              lineStyle: {
                color: "#888",
              },
            },
          },
          yAxis: {
            type: "value",
            axisLabel: {
              show: true,
              formatter: "{value}",
            },
            splitNumber: 3,
            axisLine: {
              lineStyle: {
                color: "#888",
              },
            },
          },
        },
      },
    ],
  };

  const userTableColumns = [
    {
      title: "用户",
      dataIndex: "name",
    },
    {
      title: "总市值",
      dataIndex: "total",
      render: (text) => text.toFixed(2),
      sorter: (a, b) => a.total - b.total,
    },
    {
      title: "未实现盈亏",
      dataIndex: "unrealizedPnl",
      render: (text) => text.toFixed(2),
    },
    {
      title: "未分配收益",
      dataIndex: "unrealizedAllocation",
      render: (text) => text.toFixed(2),
    },
    {
      title: "实际市值",
      dataIndex: "real",
      render: (text) => text.toFixed(2),
    },
  ];

  return (
    <Layout className={className}>
      <Divider className="divider" />
      <Content>
        <Row gutter={24} className="content">
          <Col sm={24} xs={24}>
            <Card title="T1 利润分配">
              <Row style={{ marginBottom: "24px" }}>
                <Col sm={5} xs={12}>
                  <Statistic
                    title="待分配利润（USD）"
                    value={unrealizedDividend}
                    precision={0}
                  />
                </Col>
              </Row>
              <Row>
                <Col xs={24} className="card-left">
                  {<LineChart option={userOption} />}
                </Col>
              </Row>
            </Card>
          </Col>
        </Row>
        <Row gutter={24} className="content">
          <Col sm={24} xs={24}>
            <Card title="收益明细">
              <Table columns={userTableColumns} dataSource={userTable} />
            </Card>
          </Col>
        </Row>
      </Content>
    </Layout>
  );
};

export const Info = styled(DetailsComponent)`
  .p-24 {
    padding: 24px;
  }
  .m-t-24 {
    margin-top: 24px;
  }
  .m-b-24 {
    margin-bottom: 24px;
  }
  .divider {
    border-color: #000;
    opacity: 0.8;
    margin: 0;
  }
  .content {
    padding-top: 40px;
    max-width: 1120px;
    margin: 0 auto !important;
  }

  .green {
    color: #3f8600;
  }
  .red {
    color: #cf1322;
  }
  .grey {
    color: rgba(19, 24, 31, 0.6);
  }
`;
